/* Main.Js */
import 'bootstrap';
import 'startbootstrap-sb-admin-2/css/sb-admin-2.min.css';
import 'startbootstrap-sb-admin-2/vendor/fontawesome-free/css/all.min.css';

import './assets/css/navigation.css';
import './assets/css/diagram.css';
import './assets/css/custom.css';

import React, { useContext, Component } from 'react';
import { Switch } from 'react-router-dom';
import timestamp from 'unix-timestamp';

import { ApiContext } from './assets/js/contexts/ApiContext';
import { Private } from './assets/js/utils/AccessRoute';

import Sidebar from './components/sidebar';
import Topbar from './components/topbar';
import Footer from './components/footer';

import Error, { ErrorContent } from './views/error';
import Dashboard from './views/dashboard';
import ChartNav from './views/chart-nav';
import Sensor from './views/sensor';

class Main extends Component {
    static contextType = ApiContext;
    constructor(props) {
        super(props);
        timestamp.round = true;
        this.sidebarToggle = this.sidebarToggle
            .bind(this);
            
        this.state = {
            localeTime: getLocaleTime(),
            isUpdated: false,
            toggle: true
        };
    };

    sidebarToggle(e) {
        e.preventDefault();
        this.setState(
            {toggle: !this.state.toggle}
        );
    };

    componentWillMount() {
        this.context._updateForecast();
        this.setState({
            isUpdated: this.context._getRequest(),
            localeTime: getLocaleTime()
        });
    };

    componentDidMount() {
        let [getPeriode, forecastPeriode] = [10, 600];
        let [tget, tforecast] = [
            timestamp.now(),
            timestamp.now()
        ];

        this.interval = setInterval(
            () => {
                let tnow = timestamp.now();
                if (tnow > tforecast) {
                    this.context._updateForecast();
                    tforecast = tforecast + forecastPeriode;
                } else if (tnow > tget) {
                    this.setState({
                        isUpdated: this.context._getRequest(),
                        localeTime: getLocaleTime()
                    });
                    tget = tget + getPeriode;
                };
            }, 1000
        );
    };

    componentWillUnmount() {
        clearInterval(this.interval);
    };

    render() {
        return (
            <div id="content-wrapper" className="d-flex flex-column">
                <div id="content" className={`sb-nav-fixed ${this.state.toggle ? null : "sb-sidenav-toggled"}`}>
                    <Topbar sidebarToggle={this.sidebarToggle} localeTime={this.state.localeTime} />
                    <div id="layoutSidenav">
                        <Sidebar />
                        <div id="layoutSidenav_content">
                            <Switch>
                                <Private exact path="/biogas/" component={Homepage} />
                                <Private path="/biogas/about/" component={Error} />
                            </Switch>
                            <Footer />
                        </div>
                    </div>
                </div> 
            </div>
        );
    };
};

function Homepage() {
    const last_updated = new Date
        (useContext(ApiContext)._getLastData()[0].unix * 1000)
            .toLocaleTimeString('en-GB');

    return (
        <div className="container-fluid mt-2 pt-2">
            <div className="d-flex align-items-center justify-content-between mb-3">
                <div className="d-none d-sm-inline h3 text-dark my-auto">Dashboard</div>
                <div className="d-none d-sm-inline">
                    <button className="btn btn-sm btn-warning btn-icon-split font-weight-bold" type="button">
                        <span className="icon text-white"><i className="fas fa-fw fa-sm fa-download"></i></span>
                        <span className="text px-4">Download Data</span>
                    </button>
                    <button className="btn btn-sm btn-warning mx-1" type="button">
                        <span className="icon text-white">
                            <i className="fas fa-fw fa-sm fa-redo-alt"></i>
                        </span>
                    </button>
                </div>
            </div>

            <div className="d-none d-sm-block">
                <nav className="nav nav-tabs mb-2" id="measured-data" role="tablist">
                    <a className="nav-item nav-link text-midnight font-weight-bold small active" data-toggle="tab" role="tab"
                        href="#sensor" aria-controls="sensor" aria-selected="true">Realtime Sensor Data</a>
                    <a className="nav-item nav-link text-midnight font-weight-bold small" data-toggle="tab" role="tab"
                        href="#analytical" aria-controls="analytical" aria-selected="false">Analytical</a>

                    <small className="nav-item nav-link disabled text-dark ml-auto">
                        <strong className="text-midnight">Last Updated: </strong>{last_updated}
                    </small>
                </nav>

                <div className="tab-content mb-2">
                    <div className="tab-pane fade show active" id="sensor" role="tabpanel"
                        aria-labelledby="v-pills-sensor-tab">
                        <Sensor />
                        <ChartNav />
                    </div>

                    <div className="tab-pane fade" id="analytical" role="tabpanel"
                        aria-labelledby="v-pills-biogas-tab">                 
                        <Dashboard/>
                    </div>
                </div>
            </div>

            {/* <!-- Logout Modal--> */}
            <div className="modal fade" id="logoutModal" tabindex="-1" role="dialog" aria-labelledby="modalLabel" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <small className="modal-header modal-title text-dark font-weight-bold" id="modalLabel">
                            End Session?</small>
                        <small className="modal-body text-dark">
                            Select the <strong>Logout</strong> button below to end your current session.</small>
                        <div className="modal-footer">
                            <button className="btn btn-sm btn-secondary font-weight-bold px-3" type="button" data-dismiss="modal">
                                Cancel</button>
                            <button className="btn btn-sm btn-primary font-weight-bold px-3" type="button" data-dismiss="modal"
                                onClick={useContext(ApiContext).logout}>Logout</button>
                        </div>
                    </div>
                </div>
            </div>

            {/* <!-- Edit Profile Modal --> */}
            <div className="modal fade" id="accountModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <small className="modal-header modal-title text-dark font-weight-bold" id="exampleModalLabel">
                            Account Settings</small>
                        
                        <div className="modal-body text-dark">
                            <form className="user px-3">
                                <div className="form-group">
                                    <label className="small font-weight-bold">Username</label>
                                    <input type="text" className="form-control text-monospace" placeholder="Enter username" />
                                </div>
                                <div className="form-group">
                                    <label className="small font-weight-bold">Email Address</label>
                                    <input type="email" className="form-control text-monospace" placeholder="Enter email address" />
                                </div>

                                <div className="form-group">
                                    <label className="small font-weight-bold">Old Password</label>
                                    <div className="input-group mb-3">
                                        <input type="password" className="form-control text-monospace" placeholder="Enter old password" />
                                        <div className="input-group-prepend">
                                            <button className="btn btn-warning" type="button">
                                                <a href="#"><i className="fas fa-eye-slash text-light" aria-hidden="true"></i></a>
                                            </button>
                                        </div>
                                    </div>
                                </div>

                                <div className="form-group">
                                    <label className="small font-weight-bold">New Password</label>
                                    <div className="input-group mb-3">
                                        <input type="password" className="form-control text-monospace" placeholder="Enter new password" />
                                        <div className="input-group-prepend">
                                            <button className="btn btn-warning" type="button">
                                                <a href="#"><i className="fas fa-eye-slash text-light" aria-hidden="true"></i></a>
                                            </button>
                                        </div>
                                    </div>
                                </div>

                                <div className="form-group">
                                    <label className="small font-weight-bold">Confirm New Password</label>
                                    <div className="input-group mb-3">
                                        <input type="password" className="form-control text-monospace" placeholder="Confirm new password" />
                                        <div className="input-group-prepend">
                                            <button className="btn btn-warning" type="button">
                                                <a href="#"><i className="fas fa-eye-slash text-light" aria-hidden="true"></i></a>
                                            </button>
                                        </div>
                                    </div>
                                </div>                                      
                            </form>
                        </div>

                        <div className="modal-footer">
                            <button className="btn btn-sm btn-secondary font-weight-bold px-3" type="button" data-dismiss="modal">
                                Cancel</button>
                            <button className="btn btn-sm btn-primary font-weight-bold px-3" type="button">
                                Save Changes</button>
                        </div>
                    </div>
                </div>
            </div>

            {/* <!-- 404 Error Text --> */}
            <div className="d-block d-sm-none">
                <ErrorContent message="Mobile view is not supported yet :(" />
            </div>
        </div>
    );
};

function getLocaleTime() {
    return new Date().toLocaleTimeString(
        'en-GB',
        { hour: '2-digit', minute: '2-digit' }
    );
};

export default Main;
