import React from 'react';
import { Link } from "react-router-dom";
import LocalStorageService from '../assets/js/utils/LocalStorageService';

function Sidebar() {
    return (
        <div id="layoutSidenav_nav">
            {/* <!-- Sidebar --> */}
            <nav className="sb-sidenav accordion sb-sidenav-dark" id="sidenavAccordion">
                <div className="sb-sidenav-menu">
                    <div className="nav">
                        <div className="sb-sidenav-menu-heading">Core</div>
                        <a className="nav-link small" href="#">
                            <Link to="/biogas/">
                                <div className="sb-nav-link-icon">
                                    <i className="fas fa-fw fa-home"></i>
                                    <span className="text-light font-weight-bold mx-2">Dashboard</span>
                                </div>
                            </Link>
                        </a>
                        <a className="nav-link small" href="#">
                            <Link to="/biogas/about/">
                                <div className="sb-nav-link-icon">
                                    <i className="fas fa-fw fa-question-circle"></i>
                                    <span className="text-light font-weight-bold mx-2">About</span>
                                </div>
                            </Link>
                        </a>
                        
                        <div className="sb-sidenav-menu-heading">Sections</div>
                        <a className="nav-link small" href="#measured-data">
                            <div className="sb-nav-link-icon">
                                <i className="fas fa-fw fa-database"></i>
                                <span className="text-light font-weight-bold mx-2">Measured Data</span>
                            </div>
                        </a>
                        <a className="nav-link small" href="#data-charts">
                            <div className="sb-nav-link-icon">
                                <i className="fas fa-fw fa-chart-bar"></i>
                                <span className="text-light font-weight-bold mx-2">Charts</span>
                            </div>
                        </a>
                    </div>
                </div>

                <div className="sb-sidenav-footer">
                    <h2 className="d-inline-block mt-1 mr-2">
                        <i className="fas fa-fw fa-user-circle text-white"></i>
                    </h2>

                    <div className="d-inline-block">
                        <small className="d-block text-white text-xs">Welcome Back!</small>
                        <small className="d-block text-white font-weight-bold">
                            {LocalStorageService.getUser()}
                        </small>
                    </div>

                    <div className="d-block text-uppercase text-xs font-weight-bold mx-1 my-3">
                        User Options</div>
                    <a className="nav-link small" href="#">
                        <div className="sb-nav-link-icon" data-toggle="modal" data-target="#accountModal">
                            <i className="fas fa-fw fa-cogs text-white"></i>
                            <span className="text-light font-weight-bold mx-2">Account Settings</span>
                        </div>
                    </a>
                    <a className="nav-link small" href="#">
                        <div className="sb-nav-link-icon" data-toggle="modal" data-target="#logoutModal">
                            <i className="fas fa-fw fa-sign-out-alt text-white"></i>
                            <span className="text-light font-weight-bold mx-2">Logout</span>
                        </div>
                    </a>
                </div>
            </nav>
            {/* <!-- End of Sidebar --> */}
        </div>
    )
};

export default Sidebar;