/* Auth.Js */
import 'bootstrap';
import 'startbootstrap-sb-admin-2/css/sb-admin-2.min.css';
import 'startbootstrap-sb-admin-2/vendor/fontawesome-free/css/all.min.css';
import './assets/css/custom.css';

import axios from 'axios';
import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import LocalStorageService from './assets/js/utils/LocalStorageService';

function Auth() {
    const history = useHistory();

    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    function login() {
        setLoading(true);
        if (username === '' || password === '') {
            setError('Please fill the authentication fields!');
            setLoading(false);
        } else {
            axios
                .post (
                    'http://sancaliar82.pythonanywhere.com/auth/obtain-token',
                    {username: username, password: password}
                )
                .then (response => {
                    setLoading(false);
                    LocalStorageService
                        .setRefreshToken(response.data.refresh);
                    LocalStorageService
                        .setAccessToken(response.data.access);
                    LocalStorageService
                        .setUser(response.data.user);
                    history.push('/biogas/');
                })
                .catch (error => {setError('Invalid credentials!');
                    setLoading(false);
                })
        }
    };

    return (
        <div>
            <nav className="sb-topnav navbar navbar-expand navbar-dark bg-asphalt shadow">
                <div className="navbar-brand">Biogas Monitoring</div>
            </nav>

            <div className="bg-gradient-light container-fluid py-4">
                <div className="row justify-content-center">

                    <div className="d-none d-sm-block col-md-10 col-lg-9 col-xl-8">
                        <div className="card shadow bg-light o-hidden">
                            <div className="card-header bg-square-midnight font-weight-bold text-white py-2">
                                <div className="d-flex flex-row align-items-baseline float-right">
                                    <div className="d-inline display-2"><i className="fas fa-th"></i></div>
                                    <div className="d-inline h3 mx-3">Biogas Monitoring</div>
                                </div>
                            </div>

                            <div className="card-body px-5">
                                <div className="d-flex flex-row align-items-center">
                                    
                                    <div className="col-md-10 col-lg-9 col-xl-8 mx-auto p-4">
                                        <div className="card shadow border-0 bg-gradient-midnight o-hidden">
                                            <div className="card-body p-4">
                                                
                                                <h4 className="card-title text-white mb-3 px-2">User Authentication</h4>
                                                <div className="d-block px-5 mx-auto">
                                                    {
                                                        error &&
                                                        <div className="alert alert-warning alert-dismissible fade show mb-2" role="alert">
                                                            <small className="text-center font-weight-bold">{error}</small>
                                                            <button type="button" className="close" data-dismiss="alert" aria-label="Close">
                                                                <span aria-hidden="true">&times;</span>
                                                            </button>
                                                        </div>
                                                    }
                                                    <form className="user">
                                                        <div className="form-group mb-1">
                                                            <label className="small font-weight-bold text-white">Username</label>
                                                            <input className="form-control text-monospace" type="text" required="true" value={username}
                                                                onChange={e=>setUsername(e.target.value)} />
                                                        </div>

                                                        <div className="form-group">
                                                            <label className="small font-weight-bold text-white">Password</label>
                                                            <input className="form-control text-monospace" type="password" required="true" value={password}
                                                                onChange={e=>setPassword(e.target.value)} />
                                                        </div>
                                                    </form>

                                                    <div className="d-flex flex-row justify-content-between align-items-center">
                                                        <button className="btn btn-sm btn-success float-right px-5" type="button" disabled={loading} onClick={login}>
                                                            <span className="font-weight-bold">
                                                                {loading ? 'Authenticating' : 'Login'}
                                                            </span>
                                                        </button>

                                                        <a className="small text-white font-weight-bold" href="#">
                                                            Forgot Password?</a>
                                                    </div>
                                                </div>

                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>

                            <div className="card-footer bg-light">
                                <small className="text-dark text-center font-weight-bold">
                                    <span className="d-block">Biogas Monitoring &copy; 2020 - Program Studi Teknik Fisika</span>
                                    <span className="d-block">Institut Teknologi Bandung</span>
                                </small>
                            </div>

                        </div>
                    </div>

                    <div className="d-block d-sm-none col-sm-12">
                        <div className="card shadow border-0 bg-light o-hidden">
                            <div className="card-body p-4">
                                <div className="col-sm-12 justify-content-center mb-3">
                                    <img className="img-fluid" src={require('./assets/img/svg/home-illust.svg')} alt="" />
                                </div>
                                
                                <h5 className="card-title text-center text-dark mb-4">User Authentication</h5>
                                <div className="d-block px-2 mx-auto">
                                    {
                                        error &&
                                        <div className="alert alert-warning alert-dismissible fade show rounded-pill mb-3" role="alert">
                                            <small className="text-center font-weight-bold">{error}</small>
                                            <button type="button" className="close" data-dismiss="alert" aria-label="Close">
                                                <span aria-hidden="true">&times;</span>
                                            </button>
                                        </div>
                                    }
                                    <form className="user">
                                        <div className="form-group">
                                            <input className="form-control form-control-user" type="text" placeholder="Account Username" required="true"
                                                value={username} onChange={e=>setUsername(e.target.value)} />
                                        </div>

                                        <div className="form-group">
                                            <input className="form-control form-control-user" type="password" placeholder="Password" required="true"
                                                value={password} onChange={e=>setPassword(e.target.value)} />
                                        </div>

                                        <button className="btn btn-success btn-user btn-block" type="button" disabled={loading} onClick={login}>
                                            <span className="font-weight-bold">
                                                {loading ? 'Authenticating' : 'Login'}
                                            </span>
                                        </button>
                                    </form>

                                    <div className="d-block dropdown-divider my-4"></div>
                                    <div className="row justify-content-center">
                                        <a className="text-center text-dark font-weight-bold small" href="#">
                                            Forgot Password?
                                        </a>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    );
};

export default Auth;