import React, { useContext } from 'react';
import ApexChartGauge from '../components/apexcharts/gauge';
import { ApiContext } from '../assets/js/contexts/ApiContext';
import { CardBody } from '../components/card';

function Sensor() {
    const [reactor, pressurized, normal] = useContext(ApiContext)
        ._getLastData();

    return (
        <div className="row">
            <div className="col-md-12 col-xl-4">
                <div className="card shadow rounded-0 mb-3">
                    <small className="card-header bg-asphalt text-white text-center font-weight-bold rounded-0 py-2">
                        Biogas Reactor
                    </small>
                
                    <div className="card-body">
                        <div id="sensor-carousel" className="carousel slide" data-ride="carousel">
                            <div className="carousel-inner" data-toggle="modal" data-target="#data-modal">
                                <div className="carousel-item active" data-interval="15000">
                                    <CardBody header="Temperature A" value={reactor.temperature_a}/>
                                </div>
                                <div className="carousel-item" data-interval="15000">
                                    <CardBody header="Temperature B" value={reactor.temperature_b}/>
                                </div>
                            </div>
                            {/* <!-- Modal control --> */}
                            <a className="carousel-control-prev" href="#sensor-carousel" role="button" data-slide="prev"></a>
                            <a className="carousel-control-next" href="#sensor-carousel" role="button" data-slide="next"></a>
                        </div>

                        <div className="d-block dropdown-divider my-auto"></div>
                        <ApexChartGauge chartLabel="Pressure (kPa)" chartHeight={320}
                            series={reactor.pressure}/>
                    </div>
                </div>
            </div>
                
            <div className="col-md-12 col-xl-4">                    
                <div className="card shadow rounded-0 mb-3">
                    <small className="card-header bg-asphalt text-white text-center font-weight-bold rounded-0 py-2">
                        Pressurized Storage
                    </small>
                
                    <div className="card-body">
                        <CardBody header="Temperature" value={pressurized.temperature}/>
                        <div className="d-block dropdown-divider my-auto"></div>
                        <ApexChartGauge chartLabel="Pressure (kPa)" chartHeight={320}
                            series={pressurized.pressure}/>
                    </div>
                </div>
            </div>

            <div className="col-md-12 col-xl-4">
                <div className="card shadow rounded-0 mb-3">
                    <small className="card-header bg-asphalt text-white text-center font-weight-bold rounded-0 py-2">
                        Normal Storage
                    </small>
                
                    <div className="card-body">
                        <CardBody header="Temperature" value={normal.temperature}/>
                        <div className="d-block dropdown-divider my-auto"></div>
                        <ApexChartGauge chartLabel="Pressure (kPa)" chartHeight={320}
                            series={normal.pressure}/>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Sensor;