import React, { useContext } from 'react';
import ApexChartMixed from '../components/apexcharts/mixed';
import ApexChartGauge from '../components/apexcharts/gauge';
import ApexChartLine from '../components/apexcharts/line';
import ApexChartBar from '../components/apexcharts/bar';

function Dashboard() {
    return (
        <div className="d-block">
            <div className="card shadow rounded-0 mb-3">
                <small className="card-header bg-asphalt text-white text-center font-weight-bold rounded-0 py-2">
                    Energy Monitoring</small>

                <div className="card-body">
                    <div className="row">
                        <div className="col-xl-4">
                            <div className="d-flex justify-content-between px-3">
                                <small className="d-inline font-weight-bold text-midnight">Biogas Generator</small>
                                <span className="badge badge-success px-4">State: ON</span>
                            </div>
                            
                            <div className="d-block dropdown-divider mb-0"></div>
                            <ApexChartGauge chartLabel="Power Usage" chartHeight={320}
                                series={55.82}/>

                            <div className="d-block dropdown-divider mt-3 mb-2"></div>
                            <div className="d-flex justify-content-between px-3 mb-0">
                                <div className="d-inline text-center">
                                    <div className="d-block h1 font-custom mb-0">220.98</div>
                                    <div className="d-block text-left text-midnight text-xs">
                                        Voltage (VAC)</div>
                                </div>
                                <div className="d-inline text-center">
                                    <div className="d-block h1 font-custom mb-0">1.54</div>
                                    <div className="d-block text-right text-midnight text-xs">
                                        Current (A)</div>
                                </div>
                            </div>
                        </div>

                        <div className="col-xl-8">
                            <small className="d-block font-weight-bold text-midnight px-3">
                                Realtime Power Usage</small>
                            <div className="d-block dropdown-divider mb-0"></div>
                            <ApexChartLine seriesName="Power" chartLabel="Watt" chartHeight={290}
                                group="power" />
                        </div>
                    </div>
                </div>
            </div>

            <div className="row">
                <div className="col-xl-8">
                    <div className="card shadow rounded-0 mb-3">
                        <small className="card-header bg-asphalt text-white text-center font-weight-bold rounded-0 py-2">
                            Biogas Consumption</small>
                            
                        <div className="card-body pb-2">
                            <small className="d-block font-weight-bold text-midnight px-3">
                                Daily Consumption (Last 2 Weeks)</small>
                            <div className="d-block dropdown-divider"></div>
                            <ApexChartMixed chartHeight={270} />
                        </div>
                    </div>
                </div>
            </div>

        </div>
    );
};

export default Dashboard;