import React from 'react';

export function ErrorContent({message}) {
    return (
        /* <!-- Begin Page Content --> */
        <div className="d-block text-center my-5">
            <div className="lead text-secondary mb-0">Error</div>
            <div className="error text-dark mx-auto" data-text="404">404</div>
            <div className="lead text-secondary mb-0">Page Not Found</div>
            <div className="small text-gray-500 mb-0">{message}</div>
        </div>
    );
};

function Error() {
    return (
        /* <!-- Begin Page Content --> */
        <div className="container-fluid">
            <div className="d-flex justify-content-center my-4">
                <ErrorContent message="This page is under development!" />
            </div>
        </div>
    );
};

export default Error;