import React, { useContext } from 'react';
import { ApiContext } from '../assets/js/contexts/ApiContext';

import Temperature from './temperature';
import Pressure from './pressure';
import Flow from './flow';

function ChartNav() {
    const last_updated = new Date
        (useContext(ApiContext)._getLastData()[0].unix * 1000)
            .toLocaleTimeString('en-GB');
            
    return (
        <div className="d-none d-md-block">
            <nav className="nav nav-tabs mb-2" id="data-charts" role="tablist">
                <a className="nav-item nav-link text-midnight font-weight-bold small active" data-toggle="tab" href="#temperature" role="tab"
                    aria-controls="temperature" aria-selected="true">Temperature Charts</a>
                <a className="nav-item nav-link text-midnight font-weight-bold small" data-toggle="tab" href="#pressure" role="tab"
                    aria-controls="pressure" aria-selected="false">Pressure Charts</a>
                <a className="nav-item nav-link text-midnight font-weight-bold small" data-toggle="tab" href="#flow" role="tab"
                    aria-controls="flow" aria-selected="false">Flow Rate Charts</a>

                <small className="nav-item nav-link disabled text-dark ml-auto">
                    <strong className="text-midnight">Last Updated: </strong>{last_updated}
                </small>
            </nav>

            <div className="tab-content mb-2">
                <div className="tab-pane fade show active" id="temperature" role="tabpanel" aria-labelledby="v-pills-temperature-tab">
                    <Temperature /></div>
                <div className="tab-pane fade" id="pressure" role="tabpanel" aria-labelledby="v-pills-pressure-tab">
                    <Pressure /></div>
                <div className="tab-pane fade" id="flow" role="tabpanel" aria-labelledby="v-pills-flow-tab">
                    <Flow /></div>
            </div>
        </div>
    );
};

export default ChartNav;