import React, { Component } from 'react';
import Chart from 'react-apexcharts';

const __fontDefault = 'Nunito, sans-serif';
const __fontCustom = 'Titillium Web, sans-serif';
const __sizeDefault = '12px';
const __sizeTitle = '13px';

class ApexChartLine extends Component {
    constructor(props) {
        super(props);
        this.state = {
            series: [{
                name: this.props.seriesName,
                data: [0]
            }],
            options: {
                chart: {
                    group: this.props.group,
                    id: 'apexchart',
                    type: 'line',    
                    animations: {
                        enabled: true,
                        easing: 'easein',
                        dynamicAnimation: {
                            speed: 250
                        }
                    },
                    toolbar: {
                        autoSelected: 'pan',
                        show: false
                    }
                },
                fill: {
                    type: 'gradient',
                    gradient: {
                        shadeIntensity: 1,
                        opacityFrom: 0.75,
                        opacityTo: 1.0,
                        stops: [0, 100]
                    }
                },
                stroke: {
                    curve: 'straight',
                    lineCap: 'round',
                    width: 3
                },
                xaxis: {
                    type: 'category',
                    labels: {
                        show: false,
                        style: {
                            fontFamily: __fontCustom,
                            fontSize: __sizeDefault
                        }
                    },
                    tooltip: {
                        enabled: false,
                        style: {
                            fontFamily: __fontCustom,
                            fontSize: __sizeDefault
                        }
                    }
                },
                yaxis: {
                    tickAmount: (3),
                    decimalsInFloat: 1,
                    labels: {
                        show: true,
                        style: {
                            fontFamily: __fontCustom,
                            fontSize: __sizeDefault
                        }
                    },
                    title: {
                        offsetY: 8,
                        text: this.props.chartLabel,
                        style: {
                            fontFamily: __fontCustom,
                            fontSize: __sizeTitle,
                            fontWeight: 'normal'
                        }
                    },
                    axisBorder: {
                        show: true,
                        color: '#2C3E50'
                    }
                },
                tooltip: {
                    enabled: false,
                    fillSeriesColor: false,
                    style: {
                        fontFamily: __fontCustom,
                        fontSize: __sizeDefault
                    },
                    custom: function() {
                        return ''
                    }
                },
                dataLabels: {
                    enabled: false
                },
                grid: {
                    show: true,
                    strokeDashArray: 4,
                    row: {
                        colors: ['#F3F3F3', 'transparent'],
                        opacity: 0.5
                    }
                }
            }
        };
    };

    render() {
        const series = [{
            name: this.props.seriesName,
            data: this.props.series
        }];

        return (
            <div className="d-flex flex-column mb-0">
                <Chart options={this.state.options} series={series} type="line"
                    height={this.props.chartHeight} />
            </div>
        );
    };
};

export default ApexChartLine;