import React, { Component } from 'react';
import Chart from 'react-apexcharts';

const __fontDefault = 'Nunito, sans-serif';
const __fontCustom = 'Titillium Web, sans-serif';
const __sizeDefault = '12px';
const __sizeTitle = '14px';

class ApexChartBar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            series: [{
                data: generateData([25, 75], 12)
            }],
            options: {
                chart: {
                    type: 'bar',
                    toolbar: {
                        autoSelected: 'pan',
                        show: false
                    }
                },
                colors: ['#E67E22'],
                plotOptions: {
                    bar: {
                        barHeight: 72,
                        horizontal: true,
                        endingShape: 'rounded',
                        dataLabels: {
                            position: 'bottom'
                        }
                    }
                },
                dataLabels: {
                    enabled: true,
                    textAnchor: 'start',
                    style: {
                        colors: ['#FFF'],
                        fontFamily: __fontDefault,
                        fontSize: '10px'
                    },
                    formatter: function (val, opt) {
                        return opt.w.globals.labels[opt.dataPointIndex];
                    }
                },
                xaxis: {
                    categories: ['January', 'February', 'March', 'April', 'May', 'June', 'July',
                        'August', 'September', 'October', 'November', 'December'
                    ],
                    labels: {
                        show: false
                    }
                },
                yaxis: {
                    axisTicks: {
                        show: true,
                    },
                    labels: {
                        show: false
                    }
                },
                tooltip: {
                    enabled: false,
                    style: {
                        fontFamily: __fontDefault,
                        fontSize: __sizeDefault
                    },
                    custom: function() {
                        return ''
                    }
                },
                grid: {
                    show: true,
                    strokeDashArray: 4,
                    row: {
                        colors: ['#F3F3F3', 'transparent'],
                        opacity: 0.5
                    }
                }
            }
        };
    };

    render() {
        return (
            <div className="d-flex flex-column my-0">
                <Chart options={this.state.options} series={this.state.series} type="bar"
                    height={this.props.chartHeight} />
            </div>
        );
    };
};

function generateData(range, count) {
    let i = 0;
    let series = [];
    while (i < count) {
        let y = Math.floor(
            Math.random() * (range[1] - range[0] + 1)
        ) + range[0];

        series.push(y);
        i++;
    }; return series;
};

export default ApexChartBar;