import React, { useContext } from 'react';
import ApexChartLine from '../components/apexcharts/line';
import { ApiContext } from '../assets/js/contexts/ApiContext';

function Temperature() {
    const graphDataObject = useContext(ApiContext)
        ._getGraphData();
    
    return(
        <div className="card shadow rounded-0 mb-3">
            <small className="card-header bg-asphalt text-white text-center font-weight-bold rounded-0 py-2">
                Temperature Charts</small>

            <div className="card-body pb-2">
                <div id="plot-carousel" className="carousel slide" data-ride="carousel">
                    <div className="carousel-inner" data-toggle="modal" data-target="#data-modal">
                        <div className="carousel-item active" data-interval="15000">
                            <ChartContent series={graphDataObject.reactor.temperature_a}
                                header="Biogas Reactor A" />
                        </div>
                        <div className="carousel-item" data-interval="15000">
                            <ChartContent series={graphDataObject.reactor.temperature_b}
                                header="Biogas Reactor B" />
                        </div>
                    </div>
                    {/* <!-- Modal control --> */}
                    <a className="carousel-control-prev" href="#plot-carousel" role="button" data-slide="prev"></a>
                    <a className="carousel-control-next" href="#plot-carousel" role="button" data-slide="next"></a>
                </div>

                <div className="d-flex dropdown-divider mb-4"></div>
                <ChartContent series={graphDataObject.pressurized.temperature}
                    header="Pressurized Storage" />

                <div className="d-flex dropdown-divider mb-4"></div>
                <ChartContent series={graphDataObject.normal.temperature}
                    header="Normal Storage" />
            </div>
        </div>
    );
};

function ChartContent({header, series={}}) {
    return (
        <div className="row px-2">
            <div className="col-lg-8 mx-auto">
                <div className="d-flex justify-content-between mx-auto">
                    <small className="font-weight-bold text-midnight px-2">
                        {`${header} (Last Hour):`}</small>
                </div>

                <ApexChartLine seriesName="Temperature" chartLabel="degC —°C" chartHeight={180}
                    group="temperature" series={series}/>
            </div>

            <div className="col-lg-4 mx-auto">
                <div className="d-flex justify-content-between mx-auto mb-0">
                    <small className="d-inline font-weight-bold text-midnight">Latest Data:</small>
                    <span className="badge badge-info px-4">Measured in °C</span>
                </div>

                <div className="d-flex justify-content-center font-custom display-2 mb-0">
                    {series.slice(-1)[0].y}</div>

                <div className="d-flex dropdown-divider mt-0"></div>
                <div className="d-flex justify-content-between align-items-center mb-0 px-4">
                    <div className="h2 d-inline font-custom">{getMinMax(series)[0]}</div>
                    <div className="h2 d-inline font-custom">{getMinMax(series)[1]}</div>
                </div>
            </div>
        </div>
    )
};

function getMinMax(s){return['min','max'].map(f=>{return Math[f].apply(null,Object.values(s).map(d=>d.y))})};
export default Temperature;