import React, { useContext } from 'react';
import { ApiContext } from '../assets/js/contexts/ApiContext';

function Topbar({sidebarToggle, localeTime}) {
    const forecast = useContext(ApiContext)
        ._getForecast();
    
    return (
        <div>
            {/* <!-- Topbar --> */}
            <nav className="sb-topnav navbar navbar-expand navbar-dark bg-asphalt shadow">
                <div className="navbar-brand">Biogas Monitoring</div>
                <button className="btn btn-link btn-sm" id="sidebarToggle" onClick={sidebarToggle}>
                    <i className="fas fa-bars"></i>
                </button>

                {/* <!-- Navbar Search--> */}
                <form className="navbar-search d-none d-md-block form-inline">
                    <div className="input-group mx-3">
                        <input className="form-control form-control-sm" type="text" aria-label="Search"
                            aria-describedby="basic-addon" style={{width: '240px'}} />
                        <div className="input-group-append">
                            <button className="btn btn-warning btn-sm" type="button">
                                <i className="fas fa-search fa-sm"></i>
                            </button>
                        </div>
                    </div>
                </form>

                {/* <!-- Navbar--> */}
                <ul className="navbar-nav d-none d-sm-block ml-auto">
                    <li className="nav-item d-flex justify-content-center align-items-center">
                        <div className="d-inline-block mx-1">
                            <div className="d-block text-white font-weight-bold text-right">
                                {`${forecast.temperature}°C`}</div>
                            <div className="d-block text-white text-xs">Sumber: Data BMKG</div>
                        </div>
                        <div className="d-inline-block mx-2"><img src={forecast.icon} height="50px" alt=""/></div>
                        <h2 className="d-inline-block font-custom text-white mt-1 mx-2">
                            {localeTime}</h2>
                    </li>
                </ul>
            </nav>
            {/* <!-- End of Topbar --> */}
        </div>
    );
};

export default Topbar;