import React, { Component } from 'react';
import Chart from 'react-apexcharts';

const __fontDefault = 'Nunito, sans-serif';
const __fontCustom = 'Titillium Web, sans-serif';
const __sizeDefault = '12px';
const __sizeTitle = '14px';

class ApexChartMixed extends Component {
    constructor(props) {
        super(props);
        this.state = {
            series: [{
                data: generateDayWise([25, 75], 14),
                name: 'Water Heater',
                type: 'column'
                
            }, {
                data: generateDayWise([25, 75], 14),
                name: 'Generator',
                type: 'column'
            }, {
                data: generateDayWise([25, 75], 14),
                name: 'Total Consumption',
                type: 'line'
            }],
            options: {
                chart: {
                    id: 'chart_0',
                    type: 'line',
                    stacked: false,
                    toolbar: {
                        autoSelected: 'pan',
                        show: false
                    }
                },
                plotOptions: {
                    bar: {
                        endingShape: 'rounded',
                        columnWidth: 72
                    }
                },
                xaxis: {
                    type: 'category',
                    tickPlacement: 'on',
                    labels: {
                        show: false,
                        style: {
                            fontFamily: __fontCustom,
                            fontSize: __sizeDefault
                        }
                    },
                    tooltip: {
                        enabled: false,
                        style: {
                            fontFamily: __fontCustom,
                            fontSize: __sizeDefault
                        }
                    }
                },
                yaxis: [
                    AxisConfigs({color: '#2980b9', title: 'Water Boiler (M3)', opposite: false}),
                    AxisConfigs({color: '#27ae60', title: 'Generator (M3)', opposite: true}),
                    AxisConfigs({color: '#e67e22', title: 'Total Consumption (M3)', opposite: true})
                ],
                colors: ['#2980b9', '#27ae60', '#e67e22'],
                dataLabels: {
                    enabled: false,
                    enabledOnSeries: [2],
                    style: {
                        fontFamily: __fontCustom,
                        fontSize: '10px'
                    }
                },
                stroke: {
                    colors: ['transparent', 'transparent', '#e67e22'],
                    lineCap: 'round',
                    width: [2, 2, 3]
                },
                tooltip: {
                    enabled: false,
                    style: {
                        fontFamily: __fontCustom,
                        fontSize: __sizeDefault
                    },
                    custom: function() {
                        return ''
                    }
                },
                legend: {
                    /* @param {string} seriesName - The name of the series corresponding to the legend.
                     * @param {object} opts - Contains additional information. */
                    show: false,
                    fontFamily: __fontCustom,
                    tooltipHoverFormatter: function(seriesName, opts) {
                        return(
                            seriesName +
                            ' - <strong class="text-midnight">' +
                            opts.w.globals.series[opts.seriesIndex][opts.dataPointIndex] +
                            '</strong> M3'
                        );
                    },
                    markers: {
                        radius: 2
                    }
                },
                grid: {
                    show: true,
                    strokeDashArray: 4,
                    row: {
                        colors: ['#F3F3F3', 'transparent'],
                        opacity: 0.5
                    }
                }
            }
        };
    };

    render() {
        return (
            <div className="d-flex flex-column my-0">
                <Chart options={this.state.options} series={this.state.series} type="line"
                    height={this.props.chartHeight} />
            </div>
        )
    };
};

function AxisConfigs(props={}) {
    return {
        opposite: props.opposite,
        decimalsInFloat: 1,
        axisTicks: {
            show: true,
        },
        axisBorder: {
            show: true,
            color: props.color
        },
        labels: {
            style: {
                colors: props.color,
                fontFamily: __fontCustom,
                fontSize: __sizeDefault
            }
        },
        title: {
            text: props.title,
            style: {
                color: props.color,
                fontFamily: __fontCustom,
                fontSize: __sizeTitle,
                fontWeight: 'normal'
            }
        },
        tooltip: {
            enabled: false,
            style: {
                fontFamily: __fontCustom,
                fontSize: __sizeDefault
            }
        }
    };
};

function generateDayWise(range, count) {
    let i = 0;
    let series = [];
    let baseval = Date.now();
    while (i < count) {
        let x = baseval;
        let y = Math.floor(
            Math.random() * (range[1] - range[0] + 1)
        ) + range[0];

        series.push([x, y]);
        baseval += 86400000;
        i++;
    }; return series;
};

export default ApexChartMixed;